<template>
  <a-form-model class="search-container"
                layout="inline"
                :model="searchFrom"
                @submit="handleSubmit"
                @submit.native.prevent>
    <a-form-model-item label="检索关键字">
      <a-input v-model="searchFrom.searchWord"
               placeholder="请输入关键字">
      </a-input>
    </a-form-model-item>
    <a-form-model-item label="商品类目">
      <a-select allowClear
                show-search
                placeholder="请选择商品类目"
                option-filter-prop="children"
                style="width: 200px"
                @change="handleChange">
        <a-select-option v-for="c in categoryList"
                         :key="c.id"
                         :value="c.id">
          {{ c.name}}
        </a-select-option>

      </a-select>
    </a-form-model-item>
    <a-form-model-item>
      <a-button type="primary"
                html-type="submit">
        <!-- :disabled="searchFrom.searchWord === '' || searchFrom.password === ''" -->
        搜索
      </a-button>
    </a-form-model-item>
  </a-form-model>
</template>
<script>
export default {
  props: ['categoryList'],
  data() {
    return {
      searchFrom: {
        searchWord: '',
        category: '',
      },
    };
  },
  created() {},
  methods: {
    //   提交表单时触发的函数
    handleSubmit() {
      this.$emit('submit', this.searchFrom);
    },
    // 切换类目时触发的函数
    handleChange(value) {
      this.searchFrom.category = value;
    },
  },
};
</script>
