<template>
  <a-table @change="changePage"
           :pagination="pagination"
           class="table-product-container"
           :columns="columns"
           :data-source="data">
    <div slot="operation"
         slot-scope="text, record">
      <a-button class="editBtn"
                @click="editProduct(record)"
                type="primary">
        编辑
      </a-button>
      <a-button @click="removeProduct(record)"
                type="danger">
        删除
      </a-button>
    </div>
  </a-table>
</template>

<script>
// 表头
const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
  },
  {
    title: '标题',
    dataIndex: 'title',
    key: 'title',
    ellipsis: true,
  },
  {
    title: '描述',
    dataIndex: 'desc',
    key: 'desc',
    ellipsis: true,
  },
  {
    title: '类目',
    dataIndex: 'categoryName',
    key: 'category',
    ellipsis: true,
  },
  {
    title: '预售价格',
    dataIndex: 'price',
    key: 'price',
    ellipsis: true,
  },
  {
    title: '折扣价格',
    dataIndex: 'price_off',
    key: 'price_off',
  },
  {
    title: '标签',
    dataIndex: 'tags',
    key: 'tags',
    width: 140,
  },
  {
    title: '限制购买数量',
    dataIndex: 'inventory',
    key: 'inventory',
    ellipsis: true,
  },
  {
    title: '上架状态',
    dataIndex: 'status',
    key: 'status',
    customRender: function (text, record) {
      return record.status === 1 ? '上架' : '下架';
    },
  },
  {
    title: '操作',
    dataIndex: 'operation',
    key: 'operation',
    width: 164,
    scopedSlots: { customRender: 'operation' },
  },
];

export default {
  props: ['data', 'pagination'],
  data() {
    return {
      columns,
    };
  },
  methods: {
    changePage(page) {
      this.$emit('changePage', page);
    },
    editProduct(record) {
      this.$emit('edit', record);
    },
    removeProduct(record) {
      this.$emit('del', record);
    },
  },
};
</script>

<style lang="less" scoped>
.editBtn {
  margin-right: 2px;
}
</style>