<template>
  <div class="product-list-container">
    <Search @submit="handleSubmit"
            :categoryList="categoryList" />
    <a-button class="add-btn">
      <RouterLink :to="{name:'ProductAdd'}">新增商品</RouterLink>
    </a-button>
    <TableProduct @del="delProduct"
                  @edit="editProduct"
                  @changePage="changePage"
                  :categoryList="categoryList"
                  :data="keyData"
                  :pagination="page" />
  </div>
</template>

<script>
import Search from '@/components/Search';
import TableProduct from '@/components/TableProduct';
import { getProduct, DelProduct } from '@/api/product';
import { getCategory } from '@/api/category';

export default {
  components: {
    Search,
    TableProduct,
  },
  data() {
    return {
      formData: {},
      tableData: [],
      categoryList: [],
      categoryObj: {}, // 存储categoryList
      page: {
        current: 1,
        pageSize: 10,
        showSizeChanger: true,
        total: 1,
      },
    };
  },
  async created() {
    await getCategory().then((r) => {
      this.categoryList = r.data;
      r.data.forEach((item) => {
        this.categoryObj[item.id] = item;
      });
    });
    this.getTableData();
  },
  computed: {
    keyData() {
      return this.tableData.map((item) => ({
        ...item,
        key: item.id,
      }));
    },
  },
  methods: {
    handleSubmit(form) {
      this.formData = form;
      this.page.current = 1;
      this.getTableData();
    },
    getTableData(
      config = {
        page: this.page.current,
        size: this.page.pageSize,
        ...this.formData,
      }
    ) {
      getProduct(config).then((r) => {
        this.page.total = r.total;
        this.tableData = r.data.map((item) => {
          return {
            ...item,
            categoryName: this.categoryObj[item.category]
              ? this.categoryObj[item.category].name
              : '',
          };
        });
      });
    },
    changePage(page) {
      this.page = page;
      this.getTableData();
    },
    editProduct(record) {
      console.log(record);
      this.$router.push({
        name: 'ProductEdit',
        params: {
          id: record.id,
        },
      });
    },
    delProduct(record) {
      this.$confirm({
        title: `确认删除产品?`,
        content: () => <div style="color:red;">{`${record.title}`}</div>,
        okText: '确认',
        okType: 'danger',
        cancelText: '取消',
        onOk: () => {
          DelProduct({
            id: record.id,
          }).then((r) => {
            console.log(this);
            this.getTableData();
          });
          console.log('OK');
        },
        onCancel() {
          console.log('Cancel');
        },
      });
    },
  },
};
</script>



<style lang="less" scoped>
.product-list-container {
  position: relative;

  .search-container {
    position: absolute;
    top: 20px;
    left: 40px;
  }
  .add-btn {
    position: absolute;
    top: 20px;
    right: 10px;
  }
  .table-product-container {
    position: absolute;
    top: 80px;
    left: 0;
    padding: 0 12px;
  }
}
</style>